html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  vertical-align: baseline;
  font-size: 100%;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

input {
  outline: none;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
  font: inherit;
  border: 0;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

:before, :after {
  box-sizing: border-box;
}

svg {
  color: inherit;
}

button {
  -webkit-appearance: none;
  text-align: inherit;
  box-shadow: none;
  cursor: pointer;
  color: inherit;
  font: inherit;
  background: none;
  border: none;
  border-radius: 0;
  padding: 0;
}

:root {
  --c-withe: #fff;
  --c-black: #000;
  --shadow-3d-inner: -1px -1px 4px 2px #00000040 inset;
  --shadow-3d-inner--presed: 1px 1px 4px 2px #00000040 inset;
  --shadow-3d-inner-S: -1px -1px 2px 1px #00000040 inset;
  --shadow-main: 0px 94px 37px -32px #3b555e40, 0px 64px 49px -32px #3b555ec9, 0px 25px 24px 13px #3b555e40;
  --shadow-checkbox-inner: -1px -1px 4px 0px #00000040 inset;
  --shadow-checkbox-outer: -1px -1px 2px 0px #00000040;
  --shadow-separator: 1px 1px 4px 0px #00000040;
  --shadow-3d-top-left-outer: -2px -2px 4px 0px #0000000d;
  --shadow-3d-bottom-right-outer: 2px 2px 4px 0px #00000040;
}

body {
  background: linear-gradient(180deg, var(--c-body-bg-soft) 0%, var(--c-body-bg-dark) 100%);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-width: 21.875rem;
  min-height: 100svh;
  display: flex;
}

.main-section {
  box-shadow: var(--shadow-main);
  width: 100%;
  max-width: 37.5rem;
  padding-block: 0;
  padding-inline: clamp(1.5rem, 6%, 2rem);
  overflow: hidden;
}

.main-section__head {
  justify-content: space-between;
  align-items: center;
  padding-block: 2.5rem;
  display: flex;
}

.main-section__timer {
  padding-block: 2.5rem;
}

.main-section__info {
  flex-wrap: wrap;
  align-items: stretch;
  display: flex;
}

.main-section__info__subsection {
  width: 100%;
  padding-block: 2.5rem;
}

.subsection__icon {
  margin-inline: auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-bottom: 32px;
  display: flex;
}

.subsection__body {
  padding: 1rem;
}

.subsection__body > :not(:last-child) {
  margin-bottom: 1.5rem;
}

.subsection__item {
  align-items: center;
  display: flex;
  position: relative;
}

.subsection__item__right {
  margin-left: auto;
}

@media (width >= 42.5rem) {
  .main-section {
    border-radius: 1.25rem;
    max-width: min(87.5%, 55.375rem);
  }

  .main-section__head {
    flex-direction: column;
    justify-content: space-between;
    gap: 1.25rem;
  }
}

@media (width >= 62.5rem) {
  .main-section__head, .main-section__timer {
    padding-inline: 4rem;
  }

  .main-section__info {
    flex-wrap: nowrap;
    gap: 1rem;
  }
}

.m-separator {
  background: var(--c-neutral-separator);
  box-shadow: var(--shadow-separator), var(--shadow-3d-inner-S);
  border-radius: 4px;
  flex: .25rem;
  width: 100%;
  min-width: .25rem;
  min-height: .25rem;
}

.hamburger {
  cursor: pointer;
  font-size: 2rem;
  display: block;
}

.hamburger__line {
  transform-origin: center;
  transition: y .2s ease-in .2s, transform .2s ease-in, opacity 0s .3s;
}

.active.hamburger .hamburger__line {
  transition: y .2s ease-in, transform .2s ease-in .2s, opacity 0s .3s;
}

.active.hamburger .hamburger__top, .active.hamburger .hamburger__bottom {
  y: 10.75px;
}

.active.hamburger .hamburger__top {
  transform: rotate(45deg);
}

.active.hamburger .hamburger__middle {
  opacity: 0;
}

.active.hamburger .hamburger__bottom {
  transform: rotate(-45deg);
}

.active.hamburger ~ .m-menubar__items {
  visibility: visible;
  opacity: 1;
}

.m-watch {
  aspect-ratio: 1;
  background: var(--c-watch-outer);
  box-shadow: var(--shadow-3d-top-left-outer), var(--shadow-3d-inner);
  border-radius: 50%;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.m-watch__circle {
  border-radius: 50%;
  position: absolute;
  inset: 12%;
}

.m-watch__circle--medium {
  border: 2px solid var(--c-watch-border);
  background: var(--c-watch-medium);
  box-shadow: var(--shadow-3d-inner);
}

.m-watch__circle--inner {
  background: linear-gradient(147deg, var(--c-watch-inner-top) 7.46%, var(--c-watch-inner-bottom) 90.46%);
  box-shadow: var(--shadow-3d-inner);
}

.m-watch__numbers {
  border-radius: 50%;
  position: absolute;
  inset: 0;
}

.m-watch__numbers > span {
  transform: rotate(calc(30deg * var(--i)));
  text-align: center;
  position: absolute;
  inset: 2.5%;
}

.m-watch__numbers > span > p {
  transform: rotate(calc(-30deg * var(--i)));
  display: inline-block;
}

.m-watch__counter {
  text-align: center;
  -moz-appearance: textfield;
  background: none;
  width: 2em;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.m-watch__counter::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.m-watch__counter::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.m-watch__segundero, .m-watch__minutero {
  transform: rotate(calc(6deg * var(--i)));
  border-radius: 1rem;
  position: absolute;
  inset: 6%;
}

.m-watch__segundero:after, .m-watch__minutero:after {
  content: "";
  box-shadow: var(--shadow-3d-inner);
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}

.m-watch__segundero {
  transition: transform 1s linear;
}

.m-watch__segundero:after {
  background-color: var(--c-accent-positive);
}

.m-watch__minutero {
  transition: transform .5s linear;
}

.m-watch__minutero:after {
  background-color: var(--c-accent-negative);
}

.m-button {
  aspect-ratio: 1;
  box-shadow: var(--shadow-3d-inner);
  border-radius: 50%;
  width: 100%;
  min-width: 5rem;
  position: relative;
}

.m-button--minus {
  background: var(--c-accent-negative);
}

.m-button--plus {
  background: var(--c-accent-positive);
}

.m-button__inner {
  border: 2px solid var(--c-neutral-separator);
  background: inherit;
  box-shadow: var(--shadow-3d-inner), var(--shadow-3d-bottom-right-outer);
  border-radius: 50%;
  position: absolute;
  inset: 20%;
}

.m-button__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.m-bg-main {
  background: linear-gradient(140deg, var(--c-main-bg-soft) 0%, var(--c-main-bg-dark) 100%);
}

.m-menubar {
  position: relative;
}

.m-menubar__items {
  z-index: 10;
  background: linear-gradient(147deg, var(--c-main-bg-soft) 7.46%, var(--c-main-bg-dark) 90.46%);
  box-shadow: var(--shadow-3d-inner);
  opacity: 0;
  visibility: hidden;
  border-radius: 8px;
  flex-direction: column;
  row-gap: 1rem;
  padding: 1.5rem;
  transition: all .2s;
  display: flex;
  position: absolute;
  bottom: -1rem;
  right: 0;
  transform: translate(0, 100%);
}

.m-menubar__btn {
  padding: .5rem;
  position: relative;
}

.m-menubar__btn:after {
  content: "";
  background: var(--c-accent-neutral);
  border-radius: 4px;
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.m-timer__controls {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
  padding-inline: 1.5rem;
  display: flex;
}

.m-timer__ctrl-btn {
  width: 2.5rem;
  height: 2.5rem;
}

.m-timer__watch {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: .75rem;
  margin-bottom: 1.5rem;
  display: flex;
}

.m-timer__clock {
  flex: 100%;
  max-width: 19.5rem;
  margin-inline: auto;
}

.m-timer__separator {
  width: 100%;
}

.m-timer__left-btn, .m-timer__right-btn {
  flex-direction: column;
  flex: auto;
  justify-content: center;
  align-items: center;
  gap: 24px;
  max-width: 8rem;
  display: flex;
}

.m-config-icon {
  width: 2.5rem;
  height: 2.5rem;
}

.m-data-icon {
  width: 3.125rem;
  height: 3.125rem;
}

.m-icon-line {
  background: var(--c-accent-negative);
  border-radius: 4px;
  width: 5rem;
  height: .125rem;
}

.m-checkbox__label {
  margin-left: 2.5rem;
}

.m-checkbox__label:before, .m-checkbox__label:after {
  content: "";
  box-shadow: var(--shadow-checkbox-inner), var(--shadow-checkbox-outer);
  background: var(--c-neutral-base);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.m-checkbox__label:before {
  width: 1.5rem;
  height: 1.5rem;
  left: 0;
}

.m-checkbox__label:after {
  border: 1px solid var(--c-neutral-high);
  width: 1rem;
  height: 1rem;
  left: .25rem;
}

.m-checkbox__input {
  visibility: hidden;
  position: absolute;
}

.m-cycle-input {
  background: none;
}

.m-audio {
  visibility: hidden;
}

.m-reset-button {
  box-shadow: var(--shadow-3d-inner);
  border: 1px solid #000;
  border-radius: .8rem;
  margin-inline: auto;
  padding: .8rem 1.2rem;
}

@media (width >= 42.5rem) {
  .m-menubar {
    width: 100%;
  }

  .m-menubar .hamburger {
    display: none;
  }

  .m-menubar__items {
    opacity: 1;
    visibility: visible;
    box-shadow: none;
    background: none;
    border-radius: 0;
    flex-direction: row;
    justify-content: space-between;
    row-gap: 0;
    width: 100%;
    padding: 0;
    position: static;
    transform: translate(0);
  }

  .m-timer__left-btn {
    order: -1;
  }

  .m-timer__separator {
    display: none;
  }
}

